@import '../assets/variables.scss';

.offers-wrapper {

    .offers {

        .offers-list {
            gap: 15px;
            display: inline-grid;
            grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
            width: 100%;

            .offer {
                position: relative;
                width: 100%;
                margin: auto;
                background-color: #fff;
                border-radius: 10px;
                box-shadow: $main-box-shadow;
                overflow: hidden;
                height: 160px;

                // &:hover {
                //     .offer-text {
                //         transition: margin-top .5s ease 0s;
                //         margin-top: -20%;
                //     }
                // }

                .offer-header {
                    height: 150px;
                    display: flex;
                    justify-content: space-between;
                    padding: 5px 15px;
                    position: relative;
                    color: #fff;
                    font-weight: bold;
                    background-size: cover;

                    .offer-name {
                        background-color: $main-color;
                        height: fit-content;
                        padding: 5px;
                        position: absolute;
                        bottom: 0;
                    }

                    .offer-reduce {
                        background-color: $main-color;
                        height: 110px;
                        width: 110px;
                        transform: rotate(45deg);
                        padding: 5px;
                        display: flex;
                        justify-content: center;
                        position: absolute;
                        right: -65px;
                        top: -65px;

                        span {
                            margin: auto auto 0 auto;
                            font-size: 22px;
                        }
                    }
                }

                // .offer-text {
                //     position: relative;
                //     padding: 15px;
                //     transition: margin-top .5s ease 0s;
                //     margin-top: 0%;
                //     z-index: 3;
                //     background-color: #fff;
                // }
            }
        }
    }
}