@import '../assets/variables.scss';

.header {
	background-color: #fff;
	
	.header-bar {
		display: flex;
		justify-content: space-between;
		height: 65px;
		width: 75%;
		margin: auto;
		
		.header-logo {
			display: flex;
			flex-direction: column;
			margin: 10px;
			text-align: center;
			overflow: hidden;
			height: 55px;
			cursor: pointer;
			
			&:hover {
				.header-logo_secondary {
					margin-top: -15px;
					transition: margin .5s;
				}
			}
			
			.header-logo_main {
				font-weight: bold;
				background-color: rgb(247, 130, 35);
				padding: 10px;
				color: white;
				border-radius: 5px;
				z-index: 1;
				box-shadow: $main-box-shadow;
			}
			
			.header-logo_secondary {
				font-size: 12px;
				display: flex;
				flex-direction: column;
				color: #000;
				transition: margin .5s;
				z-index: 0;
			}
		}
		
		.header-menu {
			display: flex;
			
			.header-menu_btns {
				height: fit-content;
				margin: auto;
				display: flex;
			}
			
			.header-links {
				display: flex;
				list-style: none;
				padding: 0;
				margin: 0 10px;
				
				a {
					padding: 0 10px;
					margin: 0 5px;
					color: #000;
					transition: color .3s ease;
					height: 100%;
					display: flex;
					justify-content: center;
					position: relative;
					
					span {
						margin: auto;
					}
					
					&:after {
						position: absolute;
						content: "";
						bottom: 0;
						left: 0;
						height: 4px;
						width: 0;
						transition: width .4s ease-out;
						background-color: $main-color-transparent;
					}
					
					&:hover {
						color: $main-color;
						
						&:after {
							width: 100%;
							transition: width .4s ease-out;
						}
					}
					
					&.active {
						position: relative;
						
						&:after {
							position: absolute;
							content: "";
							bottom: 0;
							left: 0;
							height: 4px;
							width: 100%;
							background-color: $main-color;
						}
					}
				}
			}
		}
	}
	
	.header-lower-bar {
		// overflow: hidden;
		background-color: #fff;
		width: 100%;
		height: 30px;
	}
}

@media only screen and (max-width: 1200px) {
	.header {
		.header-bar {
			width: 97%;
		}
	}
}