@import '../assets/variables.scss';

.delivery_wrapper {
	position: absolute;
	z-index: 1;
	background-color: #fff;
	width: 100%;
	box-shadow: $main-box-shadow;
	
	.delivery-header-wrapper {
		width: 100%;
		border-top: 1px solid #f5f5f5;
		
		&:hover {
			cursor: pointer;
		}
		
		.delivery-header {
			display: flex;
			justify-content: space-between;
			width: 50%;
			height: 30px;
			margin: auto;
			
			span {
				margin: auto 0;
				font-weight: 500;
			}
			
			.delivery-header-chevron {
				width: 17.5px;
				position: relative;
				margin: auto 0;
				
				.fa-chevron-down {
					
					transition: transform .3s ease;
				}
				
				i {
					height: 21px;
					margin: auto 0;
					font-size: 20px;
				}
			}
		}
	}
	.delivery_main-box {
		margin: auto;
		background-color: #fff;
		overflow: hidden;
		max-height: 242px;
		transition: max-height .3s ease-in-out;
		
		.delivery_main-box-header {
			font-size: 25px;
			font-weight: bold;
			margin: auto;
			width: fit-content;
		}
		
		.delivery_steps {
			width: 100%;
			display: flex;
			justify-content: center;
			
			.delivery_step {
				margin: auto .5px;
				display: flex;
				flex-direction: column;
				text-align: center;
				
				&:nth-of-type(1) {
					
					.delivery_step-img {
						position: relative;
						
						&::before {
							position: absolute;
							content: "";
							height: 10px;
							width: 1px;
							border-left: 1px solid grey;
							bottom: -5px;
							left: 0;
						}
					}
				}
				&:nth-of-type(6) {
					
					.delivery_step-img {
						position: relative;
						
						&::before {
							position: absolute;
							content: "";
							height: 10px;
							width: 1px;
							border-right: 1px solid grey;
							bottom: -5px;
							right: 0;
						}
					}
				}
				
				.delivery_step-img {
					width: 100%;
					border-bottom: 1px dotted black;
					position: relative;
					
					img {
						height: 100px;
						width: 100px;
						margin: auto;
					}
					
					&:after {
						content: "";
						height: 3px;
						width: 3px;
						background-color: black;
						position: absolute;
						bottom: -2px;
						left: 50%;
						margin: auto;
						box-shadow: 0px 5px 0 -1px black, 0px 10px 0 -1px black, 0px 15px 0 -1px black, 0px 20px 0 -1px black;
					}
				}
				
				.delivery_step-info {
					padding: 25px;
					
					.delivery_step-header {
						font-weight: bold;
						font-size: 1.1rem;
					}
					
					.delivery_step-text {
						font-size: 0.7rem;
					}
				}
			}
		}
	}
}

@media only screen and (max-width: 1200px) {
	.delivery_wrapper {
		.delivery-header-wrapper {
			.delivery-header {
				width: 75%;
			}
		}
	}
}