@import './variables.scss';

.form {
	
	.form-textarea {
		padding: 5px;
		
		label {
			padding: 0 5px;
		}
		
		textarea {
			width: 293px;
			min-height: 35px;
			height: 150px;
			font-size: 16px;
			border: 0px;
			border-bottom: 3px solid transparent;
			transition: border 0.5s;
			border-radius: 5px;
			box-shadow: $main-box-shadow;
			padding: 5px;
			
			&:focus {
				outline: none;
				border-bottom: 3px solid $main-color;
				transition: border 0.5s;
			}
		}
	}
	
	.form-select {
		padding: 5px;
		
		label {
			padding: 0 5px;
		}
		
		select {
			width: 200px;
			height: 35px;
			font-size: 16px;
			border: 0px;
			border-bottom: 3px solid transparent;
			transition: border 0.5s;
			border-radius: 5px;
			box-shadow: $main-box-shadow;
			padding-left: 5px;
			
			&:focus {
				outline: none;
				border-bottom: 3px solid $main-color;
				transition: border 0.5s;
			}
		}       
	}
	
	.form-input {
		display: flex;
		flex-direction: column;
		padding: 5px;
		
		label {
			padding: 0 5px;
		}
		
		input {
			width: 293px;
			height: 35px;
			font-size: 16px;
			border: 0px;
			border-bottom: 3px solid transparent;
			transition: border 0.5s;
			border-radius: 5px;
			box-shadow: $main-box-shadow;
			padding-left: 5px;
			
			&:focus {
				outline: none;
				border-bottom: 3px solid $main-color;
				transition: border 0.5s;
			}
		}
	}
	
	.form-button {
		height: 40px;
		width: 300px;
		background-color: $main-color;
		padding: 1px 2px;
		border: 0;
		color: #fff;
		font-weight: bold;
		border-radius: 5px;
		margin: 15px 5px 0 5px;
		box-shadow: $main-box-shadow;
		
		
		&:hover {
			cursor: pointer;
		}
	}
}